<template>
  <div id="detailed-info">
    <Tabs  type="card">
      <TabPane label="交易">
        <Table border :columns="tableColumns" :data="tableData" :row-class-name="rowClassName"></Table>
      </TabPane>
      <TabPane label="关联地址">
        <Table border :columns="tableColumns" :data="tableData"></Table>
      </TabPane>
      <TabPane label="挖矿">
        <Table border :columns="tableColumns" :data="tableData"></Table>
      </TabPane>
      <TabPane label="统计分析">
        <Table border :columns="tableColumns" :data="tableData"></Table>
      </TabPane>
    </Tabs>
  </div>
</template>

<script>
export default {
  name: 'detailed-info',
  data () {
    return {
      tableColumns: [
        {
          title: 'Name',
          key: 'name'
        },
        {
          title: 'Age',
          key: 'age'
        },
        {
          title: 'Sex',
          key: 'sex'
        },
        {
          title: 'Address',
          key: 'address'
        }
      ],
      tableData: [
        {
          name: 'lili',
          age: 36,
          sex: 'female',
          address: 'shenzhen'
        },
        {
          name: 'nini',
          age: 23,
          sex: 'female',
          address: 'guangzhou'
        },
        {
          name: 'mimi',
          age: 34,
          sex: 'female',
          address: 'zhongshan'
        },
        {
          name: 'kiki',
          age: 13,
          sex: 'female',
          address: 'foshan'
        }
      ]
    }
  },
  methods: {
    rowClassName (row, index) {
      console.log('[rowClassName] index: ' + index)
    }
  },
  mounted () {
    console.log('[Component]')
  }
}
</script>

<style lang="scss" scoped>
@import '../ui/service.scss';
#detailed-info {
  font-family: $fontFamily;
}
</style>



