<template>
  <div id="basic-info">
    <div class="address-bar"></div>
    <div class="basic-container">
      <div class="container-left"></div>
      <div class="container-right">
        <div class="chart-container"></div>
        <div class="chart-container"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'basic-info',
  data () {
    return {}
  },
  mounted () {
    console.log('[Component]')
  }
}
</script>

<style lang="scss" scoped>
@import '../ui/service.scss';
#basic-info {
  width: 100%;
  @include Layout(column);
  background-color: $backgroundColor;
  font-family: $fontFamily;

  .address-bar {
    width: 80%;
    height: 4.69vw;
    min-height: 45px;
    margin: 0 auto;
    background-color: $mainColor;
  }
  .basic-container {
    width: 80%;
    min-height: calc(48vw + 30px * 2);
    margin: 0 auto;
    box-sizing: border-box;
    padding: 30px 0;
    @include Layout;
  }
}
.container-left {
  flex: 1;
  min-height: 48vw;
  background: #ffffff;
}
.container-right {
  width: calc((100% - 20px) * 0.597);
  min-width: 48vw;
  height: 48vw;
  margin-left: 20px;
  @include Layout(column);

  .chart-container {
    width: 100%;
    height: calc((100% - 20px) * 0.5);
    background-color: #fff;
  }
}
</style>



