import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import MainPage from '@/views/MainPage'
import MinesPage from '@/views/MinesPage'
import RelationsPage from '@/views/RelationsPage'
import RelationItemPage from '@/views/RelationItemPage'
import TransactionsPage from '@/views/TransactionsPage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'MainPage',
      component: MainPage
    },
    {
      path: '/mines',
      name: 'mines',
      component: MinesPage
    },
    {
      path: '/relations',
      name: 'relations',
      component: RelationsPage
    },
    {
      path: '/relation-item/:id',
      name: 'relation-item',
      component: RelationItemPage
    },
    {
      path: '/transactions',
      name: 'transactions',
      component: TransactionsPage
    },
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    }
  ]
})



